import { Directive, ElementRef, Renderer2, Input, OnChanges} from '@angular/core';
import { Item } from '../models/item.model';

@Directive({
  selector: '[appCardsBoard]'
})
export class CardsBoardDirective implements OnChanges {
  private today: number =  Date.now();
  private delta: number;
  private sevenDays: number = 604800000;
  private oneMonth: number = 2592000000;
  private sixMonth: number = 15552000000;
  private border: string;

  @Input() public item: Item;

  constructor(private elem: ElementRef, private renderer2: Renderer2) { }

  public ngOnChanges(): void {
    this.delta = this.today - Date.parse(this.item.snippet.publishedAt);
    this.border = '5px solid red';
    if (this.delta <= this.sevenDays) { this.border = '5px solid blue'; }
    if (this.delta > this.sevenDays && this.delta <= this.oneMonth) { this.border = '5px solid green'; }
    if (this.delta > this.oneMonth && this.delta <= this.sixMonth) { this.border = '5px solid yellow'; }
    this.renderer2.setStyle(this.elem.nativeElement, 'border-bottom', this.border);
  }

}
