import { Component } from '@angular/core';
import { response } from './response/example';
import { Item } from './models/item.model';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  public title: string = 'youtube-client-app';
  public fetchResult: Item[] = [];
  public typeSort: string[];

  public setFetchResult(): void {
    this.fetchResult = response.items;
  }

  public setSortResult(criteria: string[]): void {
    this.typeSort = criteria;
  }
}
