import { Component, Output, EventEmitter, ViewChild, ElementRef, AfterViewInit } from '@angular/core';

@Component({
  selector: 'app-sort-panel',
  templateUrl: './sort-panel.component.html',
  styleUrls: ['./sort-panel.component.scss']
})
export class SortPanelComponent implements AfterViewInit {
  private sortItems: HTMLElement[] = [];
  private sortType: string;

  @Output() public sort: EventEmitter<string[]> = new EventEmitter;
  @ViewChild('dateElem', {static: false}) public dateElem: ElementRef;
  @ViewChild('viewElem', {static: false}) public viewElem: ElementRef;

  public ngAfterViewInit(): void {
    this.sortItems.push(this.dateElem.nativeElement, this.viewElem.nativeElement);
  }

  public underlineAdd(elem: HTMLElement): void {
    this.sortItems.forEach((item) => {
      item.classList.remove('underline');
    });
    elem.classList.add('underline');
  }

  public sortChange(input: string, elem?: HTMLElement, criteria?: string): void {
    if (elem) { this.underlineAdd(elem); }
    if (!criteria) {
      criteria = this.sortType;
    } else {
      this.sortType = criteria;
    }
    this.sort.emit([criteria, input]);
  }
}
