import { Pipe, PipeTransform } from '@angular/core';
import { Item } from '../models/item.model';

@Pipe({
  name: 'sortCards',
  pure: false,
})
export class SortCardsPipe implements PipeTransform {

  public transform(cardsList: Item[], criteria?: string[]): Item[] {

    let cardListfiltered: Item[] = [];

    if (!cardsList.length || !criteria) { return cardsList; }

    if (criteria[1]) {
      cardListfiltered = cardsList.filter((item) => item.snippet.title.includes(criteria[1])
      );
    } else {
      cardListfiltered = cardsList;
    }

    switch (criteria[0]) {
      case 'view':
        cardListfiltered.sort((a, b) => +b.statistics.viewCount - +a.statistics.viewCount);
        break;
      case 'date':
        cardListfiltered
          .sort((a, b) => Date.parse(b.snippet.publishedAt) - Date.parse(a.snippet.publishedAt));
        break;
      default:
        break;
    }
    return cardListfiltered;
  }

}
