import { Component, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent {
  @Output() public getFetch: EventEmitter<undefined> = new EventEmitter;
  @Output() public getSort: EventEmitter<string> = new EventEmitter;

  public showSortPanel: boolean = false;

  public toggleSortPanel(): void {
    this.showSortPanel = !this.showSortPanel;
  }

  public routeForSort(criteria: string): void {
    this.getSort.emit(criteria);
  }
}
